export default "import {\n  Accordion,\n  AccordionDetails,\n  AccordionSummary,\n  IconButton,\n  Stack,\n  Typography,\n} from \"@mui/material\";\nimport { NavBar } from \"ui-material\";\nimport ExpandMoreIcon from \"@mui/icons-material/ExpandMore\";\nimport { ReactNode, useState } from \"react\";\nimport { \n  LocalPhoneOutlined, \n  PersonOutlineOutlined,\n  CampaignOutlined \n} from \"@mui/icons-material\";\n\ninterface NavItem{\n  label: ReactNode;\n  items?: NavItem[]\n}\n\nconst NavbarLink = ({label}:NavItem)=>{\n  const [isOpen, setIsOpen] = useState(false)\n\n  return (<Typography \n    sx={{\n      display: 'flex',\n      justifyContent: 'center',\n      cursor: 'pointer',\n      color: 'text.primary'\n    }} \n    onClick={()=>setIsOpen(prev => !prev)}\n  >\n    {label} {<ExpandMoreIcon sx={{\n      transform: isOpen? 'rotateX(180deg)' : 'unset'\n    }}/>}\n  </Typography>)\n}\n\ninterface NavbarExampleProps{\n  items: NavItem[]\n}\n\nexport const NavbarExample = ({items}:NavbarExampleProps) => (\n  <NavBar\n    sx={{ background: \"white\", boxShadow: \"unset\", zIndex: 1301 }}\n    navItemsMobile={items.map(({label})=><Accordion\n      key={String(label)}\n      sx={{\n        boxShadow: \"unset\",\n        flexDirection: \"column\",\n        \"& .MuiCollapse-root\": {\n          width: 1,\n        },\n      }}\n    >\n      <AccordionSummary\n        sx={{\n          width: 1,\n        }}\n        expandIcon={<ExpandMoreIcon />}\n      >\n        <Typography>\n          {label}\n        </Typography>\n      </AccordionSummary>\n      <AccordionDetails>\n        Hola\n      </AccordionDetails>\n    </Accordion>)}\n    navItemsDesktop={items.map(({label})=><NavbarLink label={label} key={String(label)}/>)}\n    leftOptions={<Stack direction='row'>\n      <IconButton sx={{color: 'primary.main'}}>\n        <LocalPhoneOutlined />\n      </IconButton>\n      <IconButton sx={{color: 'primary.main'}}>\n        <PersonOutlineOutlined />\n      </IconButton>\n      <IconButton sx={{color: 'primary.main'}}>\n        <CampaignOutlined />\n      </IconButton>\n    </Stack>}\n  />\n);\n"